#hero-section {
  h2 {
    font-weight: 900;
    font-size: 2.5em;

    @media screen and (min-width: 960px) {
      font-size: 3em;
      margin-bottom: 0.4em;
    }
  }

  @media screen and (min-width: 960px) {
    display: flex;
    justify-content: center;
    position: relative;
  }
}

#purple-background {
  max-width: 300px;
  max-height: 300px;
  position: absolute;
  bottom: 360px;
  left: 200px;
  z-index: -25;

  @media screen and(min-width: 445px) {
    left: 225px;
  }

  @media screen and(min-width: 525px) {
    left: 325px;
    max-width: 350px;
    max-height: 350px;
    bottom: 320px;
  }

  @media screen and(min-width: 615px) {
    left: 355px;
  }

  @media screen and(min-width: 700px) {
    left: 395px;
    bottom: 270px;
    max-width: 400px;
    max-height: 400px;
  }

  @media screen and(min-width: 760px) {
    left: 445px;
  }

  @media screen and(min-width: 880px) {
    left: 505px;
  }

  @media screen and(min-width: 960px) {
    top: 5px;
    left: 105px;
    max-width: 475px;
    max-height: 475px;
  }

  @media screen and(min-width: 1200px) {
    max-width: 600px;
    max-height: 600px;
  }

  @media screen and(min-width: 2000px) {
    max-width: 700px;
    max-height: 700px;
  }
}

#blue-background {
  max-width: 150px;
  max-height: 150px;
  position: absolute;
  bottom: 40px;
  right: 245px;
  z-index: -25;

  @media screen and(min-width: 400px) {
    right: 320px;
  }

  @media screen and(min-width: 445px) {
    right: 325px;
  }

  @media screen and(min-width: 525px) {
    right: 375px;
    max-width: 175px;
    max-height: 175px;
  }

  @media screen and(min-width: 615px) {
    right: 400px;
  }

  @media screen and(min-width: 700px) {
    right: 450px;
    bottom: -5px;
    max-width: 225px;
    max-height: 225px;
  }

  @media screen and(min-width: 760px) {
    right: 490px;
    max-width: 250px;
    max-height: 250px;
  }

  @media screen and(min-width: 880px) {
    right: 530px;
  }

  @media screen and(min-width: 960px) {
    display: none;
  }
}

#hero-description {
  font-weight: 300;
}

.submit-button {
  @include submit-button;
}

#compatible-with-container {
  @media screen and (min-width: 960px) {
    margin-top: 8em;
  }
}

#compatible-with {
  margin-top: 2.3em;
  opacity: 0.4;
  font-size: 0.55em;
  font-weight: 400;
  letter-spacing: 2px;
}

#compatible-with-image-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 650px;
  margin: 0 auto;
}

.compatible-with-image-div {
  img {
    width: 60%;

    @media screen and(min-width: 960px) {
      width: 70%;
    }
  }
}

#hero-left-side-container {
  @media screen and (min-width: 960px) {
    max-width: 50%;
    text-align: left;
    align-self: center;
    margin-right: 6em;

    p {
      max-width: 80%;
      margin: 0;
    }

    .top-input-button {
      margin-top: 1.8em;
    }
  }
}

#phone-and-background-container {
  height: 670px;
  position: relative;

  #top-phone {
    width: 100%;
    max-width: 310px;
    margin: 0 auto;
  }
}
